import React, { Component } from 'react';
import { Popover, PopoverBody, PopoverHeader } from 'reactstrap';
import { getTransactionName } from '../../ggv/js/gegeUtil'

class TransactionType extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  getTransactionDetail(code) {
    var types = new Map();
    types.set("20-0", "发布服务交易，其他账户订阅服务后可以免费下载此服务号发布的所有资源。")
    types.set("9-0", "购买资源交易，所有资源可以免费预览10%，购买后可以继续下载剩余部分。")
    types.set("10-0", "删除资源交易,删除资源需要支付资源价格9999倍的手续费。资源删除后所有人都不能再下载。手续费归机器人。")
    types.set("8-0", "分享资源交易,发布账户本地资源，如果资源价格大于0则需要向发布者账号支付后才可以下载。")
    types.set("8-4", "分享基于位置的朋友圈信息。免费朋友圈信息任何人都可以查看。收费朋友圈需要付费给发布者之后才可以查看具体内容。")
    types.set("0-0", "充值转账交易，账户之间自由转账。手续费越高，机器人处理速度越快。")
    types.set("80-0", "发布基于位置的红包（可以携带个人内容或广告信息）。只有在发布红包位置附近发布过朋友圈的账户才可以参与抢红包。中奖金额随机。红包没有时间限制，金额被抢光则结束。")
    types.set("80-1", "抢红包交易。获奖金额随机。如果红包发布者限制了位置，则只能是在红包位置周围发布过朋友圈的账户才可以参与。")
    types.set("80-1-80","抢红包中奖的随机金额。同一个红包、每个账户只能抢一次。")
    types.set("60-0", "物品出售,出售个人物品，卖家需要支付保证金保障交易真实。保证金在交易完成后后自动退回个卖家。")
    types.set("60-1", "物品抢购,金额越大抢购成功机会越大，抢购结束后系统随机选取一个账户作为成功买家。未成功账户全部自动退款。")
    types.set("60-2", "抢购付款， 抢购成功后支付商品价格的10%。系统补齐剩余90%后卖家发货。卖家三天未发货买家自动获得商品保证金补偿。卖家不退保证金。")
    types.set("60-3", "交易管理。卖家发货。买家确认收货等操作。")
    types.set("60-3-2", "交易发货,抢购成功买家发送收货地址后卖家发货。")
    types.set("60-3-3", "收货确认，买家收到货后确认。卖家自动退回商品全价与保证金。")
    types.set("60-3-4", "买家放弃交易。卖家无需发货，同时获得系统补偿与保证金。")
    types.set("60-3-20", "8天退货")
    types.set("60-3-21", "退货确认")
    types.set("60-4", "抢购退款")
    types.set("60-4-30", "抢购退款,抢购未成功账户系统自动退回全部金额。")
    types.set("60-5", "交易评价")
    types.set("60-6", "机器人交易")
    types.set("60-6-1", "卖家3天未发货，系统自动终止交易。同时补偿买家商品保证金。")
    types.set("60-6-2", "自动确认")
    types.set("60-6-3", "自动关闭")
    types.set("60-6-4", "买家中奖后三天未付款，系统自动结束交易，卖家无需发货。卖家获得补偿与保证金。")
    types.set("60-6-22", "自动退货")
    types.set("60-7", "交易纠纷")
    types.set("60-7-1", "买家申请纠纷")
    types.set("60-7-2", "卖家申请纠纷")
    types.set("60-7-3", "纠纷投票")
    types.set("60-8", "纠纷处理")
    types.set("60-8-40", "纠纷退款")
    types.set("60-8-50", "投票奖励")
    let n = types.get(code)
    if (!n) {
      n = code;
    }
    return n;
  }

  render() {
    let type = this.props.type;
    let cotent = this.getTransactionDetail(type);
    let id = 'tpid' + this.props.id;
    let text = getTransactionName(type)
    return (
      <span>
        <span className='alert-primary  fade show' id={id} onClick={this.toggle}>
          {text}
        </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>{text}</PopoverHeader>
          <PopoverBody>{cotent}</PopoverBody>
        </Popover>
      </span>
    );
  }
}
export default TransactionType;
