import React, { Component } from 'react';
import { Button, Alert, CardBody, CardHeader, Popover, PopoverBody, PopoverHeader } from 'reactstrap';
import { loadBlockInfo, loadBlockTransaction } from '../../eshop/eshop.js'
import classnames from 'classnames';
import { Nav, NavItem, NavLink, Table, Badge, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, Pagination, PaginationItem, PaginationLink, } from 'reactstrap'
import AccountDialog from '../AccountDialog/AccountDialog.js';
import TransactionType from '../../conponents/TransactionType/TransactionType'

class BlockDialog extends Component {

  constructor(props) {
    super(props);
    this.state = ({ blockInfo: null, danger: false, transPage: 1 })
  }

  toggleDanger() {
    if (!this.state.danger) {
      setTimeout((() => {
        this.loadBlockInfo(this.props.height);
      }).bind(this), 1000);
    }
    this.setState({
      danger: !this.state.danger,
    });
  }

  loadBlockInfo(height) {
    loadBlockInfo(height, ((ok, blockInfo) => {
      if (ok && blockInfo)
        this.setState({ blockInfo })
    }).bind(this))
  }

  renderBlockTransactions() {
    if (this.state.blockInfo.transactions) {
      return (<Table hover bordered striped responsive size="sm">
        <thead>
          <tr>
            <th>交易类型</th>
            <th>发起账户</th>
            <th>接收账户</th>
            <th>金额</th>
            <th>手续费</th>
            <th>区块高度</th>
          </tr>
        </thead>
        <tbody>
          {this.renderTransactionItem()}
        </tbody>
      </Table>)
    } else {
      return null;
    }
  }

  renderTransactionItem() {
    let content = this.state.blockInfo.transactions.map((res) => {
      return (<tr>
        <td>< TransactionType type={(res.type)} id={res.id} /></td>
        <td><AccountDialog rsId={res.from} /></td>
        <td><AccountDialog rsId={res.to} /></td>
        <td>{res.amount} 积分</td>
        <td>{res.fee} 积分</td>
        <td>{res.height}</td>
      </tr>)
    });

    return content;
  }

  loadBlockTransaction() {
    loadBlockTransaction(this.state.blockInfo.block.height, this.state.transPage, 10, ((ok, trans) => {
      if (ok && trans) {
        this.state.blockInfo.transactions = trans;
        this.setState({ blockInfo: this.state.blockInfo })
      }
    }).bind(this))
  }

  preTransPage() {
    if (this.state.transPage > 1) {
      this.state.transPage--
    }
    this.loadBlockTransaction();
  }

  nextTransPage() {
    this.state.transPage++
    this.loadBlockTransaction();
  }

  rederBlockInfoDialog() {
    if (!this.state.blockInfo)
      return null;
    return (<Modal isOpen={this.state.danger} toggle={this.toggleDanger.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDanger.bind(this)}>交易区块明细</ModalHeader>
      <ModalBody>
        <p>区块ID:{this.state.blockInfo.block.id}</p>
        <p>区块机器人:<AccountDialog rsId={this.state.blockInfo.block.generatorId} /></p>
        <p>区块签名:{this.state.blockInfo.block.signature}</p>
        <p>区块高度:{this.state.blockInfo.block.height}</p>
        <p>交易总数:{this.state.blockInfo.block.count}</p>
        <p>交易列表:</p>
        {this.renderBlockTransactions()}
        <Pagination>
          <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preTransPage() }}>-</PaginationLink></PaginationItem>
          <PaginationItem active>
            <PaginationLink tag="button">{this.state.transPage}</PaginationLink>
          </PaginationItem>
          <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextTransPage() }}>+</PaginationLink></PaginationItem>
        </Pagination>
      </ModalBody>
      <Alert color="danger"><small>所有的合法交易都会打包进入区块链，大概每分钟产生一个新的区块。进入区块链的数据永远不可篡改、不可删除、不可伪造。</small></Alert>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDanger.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  render() {
    let id = 'bd-' + this.props.id;
    return (<span>
      <span className='alert-primary  fade show' id={id} onClick={this.toggleDanger.bind(this)}>
        {this.props.height}
      </span>
      {this.rederBlockInfoDialog()}
    </span>)
  }
}

export default BlockDialog;