import React, { Component } from 'react';
import { loadLocalAccount, saveUserSec, createPeerId, calcAccountId } from '../../ggv/js/gegeUtil'
import { Modal, ModalBody, ModalFooter, ModalHeader, FormGroup, Label, Button, Alert } from 'reactstrap'
import { CardGroup, Card, CardBody, Row, Col, InputGroup, Input, InputGroupAddon, InputGroupText } from 'reactstrap'
import DEBUGING from '../../../debug.js'

class BtbSecret extends Component {

    constructor(props) {
        super(props);
        this.state = { account: null, login: false, remember: false }
    }

    componentDidMount() {
        let account = loadLocalAccount()
        if (account) {
            this.setState({ account })
            this.secret = account.secret;
        }
        else {
            // this.setState({ login: true })
        }
    }

    toggleLogin() {
        this.setState({
            login: !this.state.login,
        });
    }

    doLogin() {
        if (this.state.secret && (this.state.secret.length > 38 || DEBUGING)) {
            let pattern = /^(?=.*[a-z])(?=.*[^a-zA-Z0-9\x00-\xff])(?=.*[A-Z])(?=.*\d)[^]{30,200}$/u

            if (!DEBUGING && !pattern.test(this.state.secret)) {
                this.setState({ err: '密碼不安全,必须包含大写字母、小写字母、数字与中文' })
                return;
            }
            this.toggleLogin()
            saveUserSec(this.state.secret)
            let account = loadLocalAccount()
            this.setState({ account })
            this.props.onSecret(this.state.secret, account);
        }
        else {
            this.setState({ err: '密碼不安全,长度不足,至少40位' })
        }
    }

    updateSecret(e) {
        if (e && e.target) {
            let secret = e.target.value;
            let rsId = calcAccountId(secret);
            this.setState({ rsId: rsId, secret })
        }
    }

    rememberPassword() {

    }


    clearAccount() {
        window.localStorage.setItem("cpeer", '');
        window.localStorage.setItem("userSec", '');
    }


    renderSimple() {
        let btnText = '登录';
        if (this.state.account && this.state.account.secret) {
            btnText = '退出'
        }
        return (<div><Button color="danger" onClick={this.toggleLogin.bind(this)} size='sm'>{btnText}</Button> {this.renderLoginDialog()}</div>)
    }

    render() {

        if ('simple' === this.props.mode) {
            return this.renderSimple()
        } else {
            let rsId = this.state.rsId;
            if (this.state.rsId)
                rsId = this.state.rsId;
            let btnText = '登录';
            if (this.state.account && this.state.account.secret) {
                btnText = '切换账号'
                rsId = this.state.account.rsId;
            }
            return (
                <div className="controls">
                    <InputGroup className="input-prepend">
                        <InputGroupAddon addonType="prepend">
                            <InputGroupText>账户:</InputGroupText>
                        </InputGroupAddon>
                        <Input id="appendedPrependedInput" size="26" value={rsId} />
                        <Button color="danger" onClick={this.toggleLogin.bind(this)}>{btnText}</Button>
                    </InputGroup>
                    {this.renderLoginDialog()}
                </div>
            );
        }
    }

    renderPassword() {
        return null;
    }

    renderErrmsg() {
        if (!this.state.err)
            return null;
        return (<Alert color="danger"><small>{this.state.err}</small></Alert>)
    }

    renderLoginDialog() {
        let placeholder = '这里输入眞客账户密码，长度大于40。每个新的密码都会自动生成一个新的账户。'
        if (this.state.account)
            placeholder = '当前登录账户为 ' + this.state.account.rsId + ',输入新密码切换用户';
        return (<Modal isOpen={this.state.login} toggle={this.toggleLogin.bind(this)}
            className={'modal-info ' + this.props.className}>
            <ModalHeader toggle={this.toggleLogin.bind(this)}>登陆/注册</ModalHeader>
            <ModalBody>
                <Row className="justify-content-center">
                    <Col md="8">
                        <Label size='small' htmlFor="appendedInputButtons"><small>账户ID: {this.state.rsId}</small></Label>
                        <Input type="textarea" name="textarea-input" id="textarea-input" rows="5"
                            placeholder={placeholder} onChange={(this.updateSecret).bind(this)} />
                        <input type="checkbox" checked={this.state.rememberPwd} onChange={this.rememberPassword.bind(this)}></input>
                        <Label className="form-check-label" check htmlFor="inline-checkbox1">记住密码</Label>
                        {this.renderPassword()}
                        <div>
                            {this.renderErrmsg()}
                            <Alert color="info"><small>新用户自己选择一个安全的密码就可以登陆，账户充值后，出售物品或参与抢购</small></Alert>
                            <Alert color="danger"><small>眞客账户安全由密码复杂度决定，如果密码丢失或被人猜出，账户内余额将丢失或永远无法使用</small></Alert>
                        </div>
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="danger" onClick={this.toggleLogin.bind(this)}>取消</Button>
                <Button color="danger" onClick={this.doLogin.bind(this)}>登陆</Button>
            </ModalFooter>
        </Modal>)
    }
}
export default BtbSecret;
