import React, { Component } from 'react';
import { Modal, ModalBody, Alert, ModalHeader, Button, FormGroup, Input, InputGroup, ModalFooter } from 'reactstrap'
import BtbSecret from '../BtbSecret/BtbSecret'
import { loadLocalAccount, simpleHash, calcAccountId, signString } from '../../ggv/js/gegeUtil'
import { Collapse, CardBody, Card, CardHeader, Label, InputGroupAddon, InputGroupText } from 'reactstrap'
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle, Table, PaginationItem, PaginationLink, Pagination } from 'reactstrap'
import { doMessage, shareEncrypt } from '../../eshop/eshop'
import DepositDialog from '../DepositDialog/DepositDialog'
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';

class MessageDialog extends Component {
    constructor(props) {
        super(props);
        this.state = { sending: false }
        this.secret = null;
        this.toRsId = props.to
        this.toPk = props.toPk;
        this.modules = {
            toolbar: [
                [{ 'header': '1' }, { 'header': '2' }, { 'font': [] }],
                [{ size: [] }], [{ align: [] }, 'direction'],
                ['bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block'],
                [{ color: [] }, { background: [] }],
                [{ 'list': 'ordered' }, { 'list': 'bullet' },
                { 'indent': '-1' }, { 'indent': '+1' }],
                ['link', 'image'],
                ['clean']
            ],
            clipboard: {
                // toggle to add extra line breaks when pasting HTML:
                matchVisual: false,
            }
        }
    }

    componentDidMount() {
        let account = loadLocalAccount();
        if (account && account.secret) {
            this.secret = account.secret;
            this.account = account;
        }
    }

    render() {
        return (<div>
            <Button color="danger" size='sm' onClick={this.toggleSending.bind(this)}>留言</Button>
            {this.renderMsgDialog()}
        </div>
        )
    }

    doSending() {
        if (!this.account) {
            this.setState({ dialogMsg: "(login)未登陆" })
            return;
        }
        let data = {
            from: this.account.rsId, to: this.toRsId, messageBlock: this.state.messageBlock,sig:this.state.sig,
            nonce: this.state.nonce, time: this.state.time, type: 'new'
        };
        doMessage(data, (ok, data) => {
            if (ok) {
                this.setState({ dialogMsg: data.ok ? "Success(成功) " : "err(失败)" })
            }
        })
    }

    toggleSending() {
        this.setState({
            sending: !this.state.sending, dialogMsg: ''
        });
    }

    updateTracking(message) {
        //get winner pk
        let time = new Date().getTime();
        let nonce = simpleHash(message + ' ' + time);
        let pk = this.toPk;
        let messageBlock = shareEncrypt(this.secret, pk, nonce, message);
        let sig = signString(this.secret,message)
        this.setState({ messageBlock: messageBlock, nonce: nonce, time, sig });
    }

    renderdialogMsg() {
        if (this.state.renderDesposit) {
            return (<div>
                <Label>{this.state.dialogMsg}</Label>
                <DepositDialog />
            </div>)
        } else
            if (this.state.dialogMsg) {
                return (<Label>{this.state.dialogMsg}</Label>)
            }
        return null;
    }

    onSecret(sec) {
        if (sec) {
            this.secret = sec
            let rsId = calcAccountId(this.userSec);
            this.setState({ rsId: rsId })
        }
    }

    updateRecipient(e) {

    }

    renderMsgDialog() {
        return (<Modal isOpen={this.state.sending} toggle={this.toggleSending.bind(this)}
            className={'modal-info ' + this.props.className}>
            <ModalHeader toggle={this.toggleSending.bind(this)}>留言</ModalHeader>
            <ModalBody>
                <FormGroup>
                    <div className="controls">
                        <InputGroup>
                            <BtbSecret onSecret={this.onSecret.bind(this)} />
                        </InputGroup>
                    </div>
                </FormGroup>
                <FormGroup>
                    <div className="controls">
                        <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>接收方):</InputGroupText>
                            </InputGroupAddon>
                            <Input id="prependedInput2" size="16" type="text" value={this.toRsId} />
                        </InputGroup>
                        <InputGroup className="input-prepend">
                            <ReactQuill
                                placeholder='Edit(此处编辑消息)'
                                modules={this.modules}
                                onChange={this.updateTracking.bind(this)} />
                        </InputGroup>
                        <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>消息加密:</InputGroupText>
                            </InputGroupAddon>
                            <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.messageBlock} />
                            <Alert color="danger"><small>账户余额全部大于0的两个账户才可以发送加密信息。信息加密后只有对方可以解密，任何其他人都无法解密。</small></Alert>
                        </InputGroup>
                    </div>
                </FormGroup>
                <FormGroup>
                    {this.renderdialogMsg()}
                </FormGroup>
            </ModalBody>
            <ModalFooter>
                <Button color="danger" onClick={this.toggleSending.bind(this)}>取消</Button>
                <Button color="danger" hidden={this.state.success} onClick={this.doSending.bind(this)}>发送</Button>
            </ModalFooter>
        </Modal>)
    }


}
export default MessageDialog;