import React, { Component } from 'react';
import { Modal, ModalBody, Alert, ModalHeader, Button, FormGroup, Input, InputGroup } from 'reactstrap'
import BtbSecret from '../BtbSecret/BtbSecret'
import { loadLocalAccount, showQRDialog, sign,verifyServerData } from '../../ggv/js/gegeUtil'
import { Collapse, CardBody, Card, CardHeader, Label, InputGroupAddon, InputGroupText } from 'reactstrap'
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle, Table, PaginationItem, PaginationLink, Pagination } from 'reactstrap'
import { doExchange, getExchangeStatus } from '../../eshop/eshop'
import AccountDialog from '../../conponents/AccountDialog/AccountDialog'
class DepositDialog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            transfer: false, dialogMsg: null, desposit: false, accordion: [false, false, false],
            selectActionTitle: '选择付款方式', selectReceiveTitle: '选择收款方式', exmsg: false,
            page: 1, exway: -1
        }
        this.secret = null;
    }

    componentDidMount() {
        let account = loadLocalAccount();
        if (account && account.secret) {
            this.secret = account.secret;
            this.account = account;
        }
    }

    render() {
        return (<div>
            <Button color="info" size="sm" onClick={this.toggleTransfer.bind(this)}>充值/提现</Button>
            {this.renderTransferDialog()}
        </div>
        )
    }

    toggleTransfer() {
        if (!this.state.transfer) {
            this.setState({ operation: -1, exmsg: null, dialogMsg: '' })
        }
        this.setState({
            transfer: !this.state.transfer,
        });
    }

    updateAmount(e) {
        if (e && e.target) {
            this.amount = e.target.value;
        }
    }

    updateRecipient(e) {
        if (e && e.target) {
            this.recipient = e.target.value;
        }
    }
    updateUserName(e) {
        if (e && e.target) {
            this.user = e.target.value;
        }
    }

    clearQr() {
        let div = document.querySelector('#qrcode');
        while (div.hasChildNodes()) {
            div.removeChild(div.lastChild);
        }
    }

    showQR(qr) {
        this.clearQr()
        showQRDialog(qr)
    }

    renderTransferDialog() {
        return (<Modal isOpen={this.state.transfer} toggle={this.toggleTransfer.bind(this)}
            className={'modal-info ' + this.props.className}>
            <ModalHeader toggle={this.toggleTransfer.bind(this)}>充值与提现</ModalHeader>
            <ModalBody>
                <FormGroup>
                    <div className="controls">
                        <BtbSecret onSecret={(sec) => {
                            if (sec) {
                                this.secret = sec;
                            }
                        }} />
                    </div>
                </FormGroup>
                <FormGroup>
                    {this.renderOptions()}
                </FormGroup>
                <FormGroup>
                    {this.renderdialogMsg()}
                </FormGroup>
            </ModalBody>
        </Modal>)
    }

    toggleAccordion(tab) {
        const prevState = this.state.accordion;
        const state = prevState.map((x, index) => tab === index ? !x : false);
        //load
        if (tab == 2 && !this.state.accordion[2]) {
            this.loadMyrecords()
        }
        this.setState({
            accordion: state,
            dialogMsg: '',
            exmsg: false
        });
        this.clearQr()
        //
    }
    renderOptions() {
        let t1 = '微信账号付款'
        let t2 = '支付宝账号付款'
        let s1 = '微信账号收款'
        let s2 = '支付宝账号收款'
        let s3 = '银行卡账号收款'
        return (<div>
            <Card>
                <CardHeader id="headingOne">
                    <Button block color="link" className="text-left m-0 p-0" onClick={(e) => this.toggleAccordion(0)} aria-expanded={this.state.accordion[0]} aria-controls="collapseOne">
                        <h5 className="m-0 p-0">我要充值</h5>
                    </Button>
                </CardHeader>
                <Collapse isOpen={this.state.accordion[0]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                    <CardBody>
                        <Dropdown isOpen={this.state.selectPayMethod} toggle={
                            (() => {
                                this.setState({
                                    selectPayMethod: !this.state.selectPayMethod,
                                });
                            }).bind(this)
                        }>
                            <DropdownToggle caret>
                                {this.state.selectActionTitle}
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem onClick={(() => {
                                    this.setState({ selectActionTitle: t1, exway: 0 })
                                }).bind(this)}>{t1}</DropdownItem>
                                <DropdownItem onClick={(() => {
                                    this.setState({ selectActionTitle: t2, exway: 1 })
                                }).bind(this)}>{t2}</DropdownItem>
                            </DropdownMenu>
                        </Dropdown>
                        <FormGroup>
                            <div className="controls">
                                <InputGroup className="input-prepend">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>充值):</InputGroupText>
                                    </InputGroupAddon>
                                    <Input id="appendedPrependedInput" size="5" type="text" onChange={this.updateAmount.bind(this)} placeholder='输入充值数量' />
                                </InputGroup>
                            </div>
                        </FormGroup>
                        <Button color="danger" onClick={this.doDeposit.bind(this)}>发送请求</Button>
                    </CardBody>
                </Collapse>
            </Card>
            <Card hidden={this.state.exmsg}>
                <CardHeader id="headingOne">
                    <Button block color="link" className="text-left m-0 p-0" onClick={(e) => this.toggleAccordion(1)} aria-expanded={this.state.accordion[1]} aria-controls="collapseOne">
                        <h5 className="m-0 p-0">我要提现</h5>
                    </Button>
                </CardHeader>
                <Collapse isOpen={this.state.accordion[1]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                    <CardBody>
                        {
                            this.renderWithDraw(s1, s2, s3)
                        }
                    </CardBody>
                </Collapse>
            </Card>
            <Card hidden={this.state.exmsg}>
                <CardHeader id="headingOne">
                    <Button block color="link" className="text-left m-0 p-0" onClick={(e) => this.toggleAccordion(2)} aria-expanded={this.state.accordion[2]} aria-controls="collapseOne">
                        <h5 className="m-0 p-0">我的申请记录</h5>
                    </Button>
                </CardHeader>
                <Collapse isOpen={this.state.accordion[2]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                    <CardBody>
                        <Table hover bordered striped responsive size="sm">
                            <thead>
                                <tr>
                                    <th>申请金额(积分)</th>
                                    <th>支付金额(积分)</th>
                                    <th>渠道</th>
                                    <th>申请时间</th>
                                    <th>最新状态</th>
                                    <th>操作</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.renderExchangelist()}
                            </tbody>
                        </Table>
                        <nav>
                            <Pagination>
                                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>上一页</PaginationLink></PaginationItem>
                                <PaginationItem active>
                                    <PaginationLink tag="button">{this.state.page}</PaginationLink>
                                </PaginationItem>
                                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>下一页</PaginationLink></PaginationItem>
                            </Pagination>
                        </nav>
                    </CardBody>
                </Collapse>
            </Card>
            {this.renderExMsg()}
            <div id='qrcode'></div>
        </div>)
    }

    renderWithDraw(s1, s2, s3) {
        if (true)
            return (<a href='https://github.com/btbresource/btb-android'>点击下载眞客客户端提现</a>)
        return (
            <div><Dropdown isOpen={this.state.selectReceiveMethod} toggle={
                (() => {
                    this.setState({
                        selectReceiveMethod: !this.state.selectReceiveMethod,
                    });
                }).bind(this)
            }>
                <DropdownToggle caret>
                    {this.state.selectReceiveTitle}
                </DropdownToggle>
                <DropdownMenu>
                    <DropdownItem onClick={(() => {
                        this.setState({ selectReceiveTitle: s1, exway: 0 })
                    }).bind(this)}>{s1}</DropdownItem>
                    <DropdownItem onClick={(() => {
                        this.setState({ selectReceiveTitle: s2, exway: 1 })
                    }).bind(this)}>{s2}</DropdownItem>
                    <DropdownItem onClick={(() => {
                        this.setState({ selectReceiveTitle: s3, exway: 2 })
                    }).bind(this)}>{s3}</DropdownItem>
                </DropdownMenu>
            </Dropdown>
                <FormGroup>
                    <div className="controls">
                        <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>姓名:</InputGroupText>
                            </InputGroupAddon>
                            <Input id="appendedPrependedInput" size="5" type="text" onChange={this.updateUserName.bind(this)} placeholder='输入收款人姓名' />
                        </InputGroup>
                        <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>账号:</InputGroupText>
                            </InputGroupAddon>
                            <Input id="appendedPrependedInput" size="5" type="text" onChange={this.updateRecipient.bind(this)} placeholder='输入收款账号' />
                        </InputGroup>
                        <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>提现金额:</InputGroupText>
                            </InputGroupAddon>
                            <Input id="appendedPrependedInput" size="5" type="text" onChange={this.updateAmount.bind(this)} placeholder='输入金额' />
                        </InputGroup>
                    </div>
                </FormGroup>
                <Button color="danger" onClick={this.doWithDraw.bind(this)}>发送请求</Button>
            </div>)
    }

    prePage() {
        this.state.page--
        this.loadMyrecords()
    }

    nextPage() {
        this.state.page++
        this.loadMyrecords()
    }

    loadMyrecords() {
        let token = sign(this.account.secret, this.account.pk)
        let req = { pk: this.account.pk, rsId: this.account.aid, page: this.state.page, token }
        doExchange(req, '0', '2', '0', ((ok, data) => {
            if (ok && data && data.ok) {
                this.setState({ list: data.list })
            }
        }).bind(this))
    }

    renderExchangelist() {
        let list = this.state.list;
        if (!list)
            return null;
        let content = list.map((res) => {
            let d = new Date();
            d.setTime(res.time)
            let btime = d.toLocaleString()
            //
            d.setTime(res.update)
            let update = d.toLocaleString()
            return (<tr>
                <td>{res.amount}</td>
                <td>{res.random}</td>
                <td>{res.channel}</td>
                <td>{btime}</td>
                <td>{getExchangeStatus(res.status)}</td>
                <td>{this.renderActionBtn(res)}</td>
            </tr>)
        });

        return content;
    }

    showQR2(res) {
        if (res && res.qr)
            this.showQR(res.qr)
    }

    renderActionBtn(res) {
        if (res.status == 0 && res.op == 0) {
            return (<div>
                <Button color="danger" size="sm" onClick={(e) => this.showQR2(res)}>付款码</Button>
                <Button color="danger" size="sm" onClick={(e) => this.payNotify(res)}>已经付款</Button>
            </div>)
        }
        return null;
    }

    payNotify(ex) {
        let token = sign(this.account.secret, this.account.pk)
        let req = { pk: this.account.pk, rsId: this.account.aid, token, eid: ex.eid };
        doExchange(req, '0', 9, '0', ((ok, data) => {
            if (ok && data && data.ok) {
                this.setState({ dialogMsg: '(提交成功)' })
            } else {
                this.setState({ dialogMsg: '出错了' })
            }
        }).bind(this))
    }

    doWithDraw() {
        let token = sign(this.account.secret, this.account.pk)
        let req = { pk: this.account.pk, rsId: this.account.aid, recipient: this.recipient, user: this.user, token };
        doExchange(req, this.amount, 1, this.state.exway, (ok, data) => {
            if (ok && data) {
                if (data.ok)
                    this.setState({ exmsg: true, examount: data.amount, operation: 1, dialogMsg: data.msg })
                else
                    this.setState({ dialogMsg: data.msg })
                //this.showQR(data.qr)
            } else {
                this.setState({ dialogMsg: '.出错了' })
            }
        }, this.recipient)
    }

    doDeposit() {
        if (this.state.exway < 0) {
            this.setState({ dialogMsg: '请选择付款方式' })
            return;
        }
        if (!this.account) {
            this.setState({ dialogMsg: '请登录' })
            return;
        }
        if (!this.amount || this.amount.length < 1) {
            this.setState({ dialogMsg: '请输入充值金额' })
            return;
        }

        let token = sign(this.account.secret, this.account.pk)
        let req = { pk: this.account.pk, rsId: this.account.aid, token, client: 'web' };
        doExchange(req, this.amount, 0, this.state.exway, ((ok, data) => {
            if (ok && data && data.ok) {
                if (verifyServerData(data.qr, data.signature)) {
                    this.setState({ exmsg: true, examount: data.amount, operation: 0, dialogMsg: data.msg })
                    if (data.qr)
                        this.showQR(data.qr)
                } else {
                    this.setState({ dialogMsg: '服务器数据验证错误' })
                }
            } else {
                this.setState({ dialogMsg: '出错了' })
            }
        }).bind(this))
    }

    renderExMsg() {
        if (!this.state.exmsg)
            return null;
        if (this.state.operation == 0)
            return (<Label>请扫描下方二维码支付 <span style={{ color: 'red' }}>{this.state.examount} </span>积分,支付完成后5分钟内到账.转账金额务必精确到小数点后二位，否则会影响系统进度。二维码半小时内有效，请在有效期内完成付款。</Label>)
        if (this.state.operation == 1)
            return (<Label>请转账 {this.state.examount} 到下面账号,支付确认后两小时内提现到账.</Label>)
    }

    renderdialogMsg() {
        if (this.state.dialogMsg) {
            return (<Label>{this.state.dialogMsg}</Label>)
        }
        return null;
    }
}
export default DepositDialog;