function purchaseResource(id, userSec, fee, deadline, showPurchaseStatus, pk) {
    var aid = createPeerId(userSec).aid;
    var data = { action: 'transaction', type: 'buyres', resId: id, accountId: aid, fee: fee + '', deadline: deadline + '', pk }
    doBShopTransaction(userSec, data, (ok, msg) => {
        if (ok && msg && msg.ok) {
            showPurchaseStatus(true, '')
        } else {
            showPurchaseStatus(false)
        }
    }, '')
  }

function doBShopTransaction(secret, data, cb,parameters) {
    let pk = createPeerId(secret).pk;
    data.pk = pk;
    let url = getShareHost() + "/bshop"
    postSafe(url, data, (ok, res) => {
        if (ok && res.ok) {
            submitShopTransaction(url, secret, res, cb,parameters)
        } else {
            cb(false, res)
        }
    })

}


