function shareEncrypt(secret, pkHex, nonce, data) {
    let sk = getShareKey(secret, pkHex, nonce);
    sk = new Buffer(sk).toString();
    var ciphertext = AES.encrypt(data, sk);
    ciphertext = ciphertext.toString();
    return ciphertext;
}


function shareDecrypt(secret, pkHex, nonce, block) {
    let sk = getShareKey(secret, pkHex, nonce);
    sk = new Buffer(sk).toString();
    var ciphertext = AES.decrypt(block, sk);
    ciphertext = ciphertext.toString();
    let clear = new Buffer(ciphertext, 'hex').toString();
    return clear;
}

function getIssueReason(reason) {
    let res;

    if (1 == reason) {
        res = '买家申请纠纷流程'
    } else if (2 == reason) {
        res = '卖家申请纠纷流程'
    } else if (0 == reason) {
        res = ''
    } else {
        res = '其他原因'
    }
    return res
}

function getExchangeStatus(status) {
    let res;
    if (0 == status) {
        res = '未付款'
    } else if (1 == status) {
        res = '等待系统确认'
    } else if (2 == status) {
        res = '充值完毕'
    } else {
        res = status
    }
    return res
}

function getPieceLength(bytes, p) {
    bytes /= 1024
    for (p = 20; 13 < p && bytes < 2 << p; p--);
    return 2 << p + (bytes >= ((2 << p) + (2 << p + 1)) / 2)
}

function getTorrentHash(data) {
    const chunkSize = getPieceLength(data.length) * 2
    let pieces = []
    var buffer = new Buffer(data);
    let i = 0;
    //
    let start = chunkSize * i;
    let end = chunkSize * (i + 1);
    while (start < buffer.length) {
        if (end > buffer.length)
            end = buffer.length;
        let chunk = buffer.slice(start, end);
        pieces[i] = sync(chunk)
        i++;
        start = chunkSize * i;
        end = chunkSize * (i + 1);
    }
    let bf = Buffer.from(pieces.join(''), 'hex')
    var _info = { length: buffer.length, pieces: bf, 'piece length': chunkSize }
    var infoBuffer = bencode.encode(_info)
    var infoHash = sync(infoBuffer)
    return infoHash
}

