import React, { Component } from 'react';
import { Popover, PopoverBody, PopoverHeader } from 'reactstrap';

class PopHint extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  render() {
    let id = 'ph-' + this.props.id;
    let title = this.props.title;
    if (!title)
      title = this.props.text;
    return (
      <span>
        <span className='alert-primary  fade show' id={id} onClick={this.toggle}>
          {this.props.text}
        </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>{title}</PopoverHeader>
          <PopoverBody>{this.props.content}</PopoverBody>
        </Popover>
      </span>
    );
  }
}
export default PopHint;