import React, { Component } from 'react';
import { Button, Card, CardBody, CardHeader, Popover, PopoverBody, PopoverHeader } from 'reactstrap';
import { loadAccountTransactions, loadAutoTransactions, loadAccountInfo } from '../../eshop/eshop.js'
import classnames from 'classnames';
import { Nav, NavItem, NavLink, Table, Badge, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, Pagination, PaginationItem, PaginationLink, } from 'reactstrap'
import { getShareHost } from '../../ggv/js/gegeUtil'
import BlockDialog from '../BlockDialog/BlockDialog'
import MessageDialog from '../MessageDialog/MessageDialog'
import TransactionType from '../TransactionType/TransactionType'
let host = getShareHost();
const get = require('simple-get')
class AccountDialog extends Component {
  constructor(props) {
    super(props);
    this.state = { page: 1, transPage: 1, showAccount: false, accountInfo: null, transactionTab: '0', autoTransactions: [] }
  }

  showAccountInfo(rsId) {
    this.toggleAccount();
    loadAccountInfo(rsId, 1, 10, ((ok, accountInfo) => {
      if (ok && accountInfo) {
        let accountTransactions = accountInfo.transactions;
        this.setState({ accountInfo, accountTransactions })
      }
    }).bind(this))
  }

  toggleAccount() {
    this.setState({
      showAccount: !this.state.showAccount,
    });
  }

  renderAutoTransactionItem() {
    let content = this.state.autoTransactions.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      return (<tr>
        <td><TransactionType type={res.type} id={res.id} /></td>
        <td>{res.from}</td>
        <td>{res.to}</td>
        <td>{res.amount} 积分</td>
        <td>0 积分</td>
        <td>{btime}</td>
        <td><BlockDialog height={res.height} /></td>
      </tr>)
    });
    return content;
  }

  renderAccountTransactionItem() {

    let content = this.state.accountTransactions.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      return (<tr>
        <td><TransactionType type={res.type} id={res.id} /></td>
        <td>{res.from}</td>
        <td>{res.to}</td>
        <td>{res.amount} 积分</td>
        <td>{res.fee} 积分</td>
        <td>{btime}</td>
        <td><BlockDialog height={res.height} /></td>
      </tr>)
    });

    return content;
  }

  renderAccountTransactions() {

    if (this.state.transactionTab == '0') {
      if (this.state.accountTransactions) {
        return (<Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>交易类型</th>
              <th>发起账户</th>
              <th>接收账户</th>
              <th>金额</th>
              <th>手续费</th>
              <th>时间</th>
              <th>区块高度</th>
            </tr>
          </thead>
          <tbody>
            {this.renderAccountTransactionItem()}
          </tbody>
        </Table>)
      } else {
        return null;
      }
    } else {
      //show auto transactions
      if (this.state.autoTransactions) {
        return (<Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>交易类型</th>
              <th>发起账户</th>
              <th>接收账户</th>
              <th>金额</th>
              <th>手续费</th>
              <th>时间</th>
              <th>区块高度</th>
            </tr>
          </thead>
          <tbody>
            {this.renderAutoTransactionItem()}
          </tbody>
        </Table>)
      } else {
        return null;
      }
    }
  }

  onTransactionTabClicked(tab) {
    if (this.state.transactionTab !== tab) {
      this.setState({
        transactionTab: tab,
      });
      //
      if (tab === '0') {
        //load account transactions
        this.loadAccountTransactions(1);
      }
      if (tab === '1') {
        //load auto transaction
        this.loadAutoTransactions(1);
      }
    }
  }

  renderTransactionsTabs() {
    return (<div><Nav tabs>
      <NavItem>
        <NavLink
          className={classnames({ active: this.state.transactionTab === '0' })}
          onClick={() => { this.onTransactionTabClicked('0'); }}
        >
          <i className="icon-calculator"></i><Badge
            color="success">账户交易) </Badge>
        </NavLink>
      </NavItem>
      <NavItem>
        <NavLink
          className={classnames({ active: this.state.transactionTab === '1' })}
          onClick={() => { this.onTransactionTabClicked('1'); }}
        >
          <i className="icon-basket-loaded"></i><Badge pill color="danger">机器人交易</Badge>
        </NavLink>
      </NavItem>
    </Nav>
      {this.renderAccountTransactions()}
      {this.renderTransactionPagnations()}
    </div>
    )
  }

  loadAccountTransactions(page) {
    let rsId = this.state.accountInfo.rsId;
    loadAccountTransactions(rsId, page, 10, ((ok, data) => {
      if (ok && data) {
        let accountTransactions = data.transactions;
        this.setState({ accountTransactions })
      }
    }).bind(this))
  }

  loadAutoTransactions(page) {
    let rsId = this.state.accountInfo.rsId;
    loadAutoTransactions(rsId, page, 10, ((ok, data) => {
      if (ok && data) {
        let autoTransactions = data.transactions;
        this.setState({ autoTransactions })
      }
    }).bind(this))
  }

  preTransPage() {
    if (this.state.transPage > 1) {
      this.state.transPage--
    }
    if (this.state.transactionTab === '0') {
      this.loadAccountTransactions(this.state.transPage)
    } else {
      this.loadAutoTransactions(this.state.transPage);
    }
  }

  nextTransPage() {
    this.state.transPage++
    if (this.state.transactionTab === '0') {
      this.loadAccountTransactions(this.state.transPage)
    } else {
      this.loadAutoTransactions(this.state.transPage);
    }
  }

  renderTransactionPagnations() {
    return (<Pagination>
      <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preTransPage() }}>-</PaginationLink></PaginationItem>
      <PaginationItem active>
        <PaginationLink tag="button">{this.state.transPage}</PaginationLink>
      </PaginationItem>
      <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextTransPage() }}>+</PaginationLink></PaginationItem>
    </Pagination>)
  }

  renderSpecialAccount(rsId) {
    if ('BT-4B7G-ERNV-QMMJ-HULB5' === rsId) {
      return <p style={{ color: 'red' }}>此账号为B拼平台专用交易账号</p>
    }
    return null;
  }

  rederAccountInfoDialog() {
    if (!this.state.accountInfo)
      return null;
    return (<Modal isOpen={this.state.showAccount} toggle={this.toggleAccount.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleAccount.bind(this)}>账户明细</ModalHeader>
      <ModalBody>
        <p>账户ID: {this.state.accountInfo.rsId}</p>
        {this.renderSpecialAccount(this.state.accountInfo.rsId)}
        <p>账户公钥: {this.state.accountInfo.pk}</p>
        <p>账户余额: {this.state.accountInfo.balance}积分</p>
        <div><MessageDialog to={this.state.accountInfo.rsId} toPk={this.state.accountInfo.pk} /></div>
        <p>交易列表:</p>
        {this.renderTransactionsTabs()}
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleAccount.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  render() {
    return (<div>{this.rederAccountInfoDialog()}<a href="#" onClick={(e) => { e.preventDefault(); this.showAccountInfo(this.props.rsId) }}>{this.props.rsId}</a></div>)
  }
}
export default AccountDialog;